import Vue from 'vue';

Vue.mixin({
	computed: {
		user () {
			return this.$store.getters["auth/user"] || {};
		},
		isAuthenticated () {
			return this.$store.getters["auth/isAuthenticated"];
		},
		isVerified () {
			return this.user.isVerified || false;
		},
		isPro () {
			const activeStatus = ['active', 'trialing'];
			return activeStatus.includes(this.user.subscription.status) || false;
		},
		isPastDue () {
			const pastDueStatus = ['past_due', 'incomplete'];
			return pastDueStatus.includes(this.user.subscription.status) || false;
		}
	},
	methods: {
		async setStorageCookie (key, value, expire) {
			const cookie = { value, expires: Date.now() + (expire * 1000) };
			const store = {};
			store['cookie/' + key] = JSON.stringify(cookie);
			return new Promise(resolve => {
				this.$chrome.storage.local.set(store, resolve);
			});
		},
		async getStorageCookie (key) {
			const cookieKey = 'cookie/' + key;
			const store = await new Promise(resolve => { this.$chrome.storage.local.get([cookieKey], resolve); });
			if (typeof store[cookieKey] !== "undefined") {
				const cookie = JSON.parse(store[cookieKey]);
				if (cookie.expires <= Date.now()) {
					this.deleteStorageCookie(key);
				} else {
					return cookie.value;
				}
			}
			return false;
		},
		deleteStorageCookie (key) {
			this.$chrome.storage.local.remove('cookie/' + key);
		},
		makeToast(body) {
			this.$bvToast.toast(body, {
				autoHideDelay: 2000,
				noCloseButton: true,
				toaster: 'b-toaster-bottom-center'
			})
		},
		openExternal (url) {
			this.$chrome.tabs.create({ url });
		},
		openApp (path) {
			this.openExternal(process.env.VUE_APP_AMINO_APP_URL + path);
		}
	}
});
