import Vue from 'vue';
import VueRouter from 'vue-router'
Vue.use(VueRouter)

const EditorView = () => import(/* webpackChunkName: "group-editor" */ '@/views/Editor');
const SignInView = () => import(/* webpackChunkName: "group-auth" */ '@/views/SignIn');
const RateUsView = () => import(/* webpackChunkName: "group-editor" */ '@/views/RateUs');

const requiresAuth = true;

const routes = [
  { path: '/sign-in/', component: SignInView, meta: { bodyClass: 'popup' } },
  { path: '/rate-us/:slide?', component: RateUsView, meta: { bodyClass: 'popup' } },
  { name: 'devtools', path: '/devtools', component: EditorView, meta: { requiresAuth } },
  { path: '/:type?', component: EditorView, meta: { bodyClass: 'popup', requiresAuth } }
];

const router = new VueRouter({
  routes
});

router.beforeEach((to, from, next) => {
  if (Object.prototype.hasOwnProperty.call(from.meta, "bodyClass")) {
    document.body.classList.remove(to.meta.bodyClass);
  }
  if (Object.prototype.hasOwnProperty.call(to.meta, "bodyClass")) {
    document.body.classList.add(to.meta.bodyClass);
  }

  next();
});

export default router;
