import Chrome from './Chrome';
const VueChrome = {
  install(Vue, options) {
    const opts = options || {};
    const chrome = opts.chrome || window.chrome;
    if (typeof chrome === "undefined") {
      console.log("VueChrome::Error", "Not running in a Google Chrome Extension!!");
    } else {
      Vue.prototype.$chrome = new Chrome(chrome);
    }
  }
};

export default VueChrome;
