class Chrome {

  constructor (chrome, state) {
    this.chrome = chrome;
    this.state = state || {};
  }

  get instance () {
    return this.chrome;
  }

  get storage () {
    return this.chrome.storage;
  }

  get devtools () {
    return this.chrome.devtools;
  }

  get runtime () {
    return this.chrome.runtime;
  }

  get tabs () {
    return this.chrome.tabs;
  }

  get windows () {
    return this.chrome.windows;
  }

  get activeTab () {
    return new Promise(resolve => {
      this.instance.tabs.query({active: true, windowType: 'normal', lastFocusedWindow: true}, tabs => {
        this.state.lastValidTab = tabs[0] || this.state.lastValidTab;
        resolve(this.state.lastValidTab);
      }); 
    });
  }

  get canModifyTabContent () {
    return this.activeTab.then(tab => {
      if (tab.url.match("chrome://") || tab.url.match("chrome.google.com") || tab.url.match("chrome-extension://")) {
        return false;
      }

      return true;
    });
  }
}

export default Chrome
